import React, {Component} from 'react';
import {book} from "../Data/dataBook"
import {Col, Row} from "react-bootstrap";
import "./Detail.css"
import {Link} from "react-router-dom";

export default class Detail extends Component {
    constructor(props) {
        super(props);
    }

    drawChapterLink() {
        var length = this.props.book.data.length;
        var data = this.props.book.data
        var array = [];
        console.log(length);
        for (var i = length - 1; i >= 0; i--) {
            array.push(
                <div style={{paddingLeft : '0.5vw', backgroundColor: (i % 2 == 0) ? '#B2EBF2' : 'white'}}>
                    <Link className={"Detail-chapter-text"} to={"/"}>{`Chapter ${data[i].chapter}`}</Link>
                </div>
            )
        }

        return array;
    };

    render() {
        const book = this.props.book;
        return (
            <div className={"Detail-container"}>
                <div className={"Detail-info"}>
                    <div className={"show-grid Detail-descr"}>
                        <Col className={"Detail-img"}
                             style={{backgroundImage: `url("${book.url}")`}}/>
                        <Col className={"Detail-content"}>
                            <p style={{fontSize: '6vh'}}>Giới thiệu chung : </p>
                            {book.descr}
                        </Col>
                    </div>
                    <div className={"Detail-descr-chapter"}>
                        <div className={"Detail-title"}>
                            <p className={"Detail-title-text"}>Danh sách chương</p>
                        </div>
                        {this.drawChapterLink()}
                    </div>
                </div>
            </div>
        );
    }
}


