import React, {Component} from 'react';
import "./Page.css"

class Page extends Component {
    constructor(props) {
        super(props);
        // When the user scrolls down 20px from the top of the document, show the button
        window.onscroll =
            this.scrollFunction.bind(this);

    }

    renderImg() {
        var array = [];
        var book = this.props.book;
        console.log(book);

        var chapter = this.props.chapter - 1;
        var data = book.data[chapter];


        for (var i = 0; i < data.urls.length; i++) {
            array.push(
                <img className={"Image-container"} src={data.urls[i]}/>
            );
            array.push(
                <br/>
            );
        }
        return array;
    }

    scrollFunction() {
        if (document.getElementById("topBtn") == null)
            return;
        if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
            document.getElementById("topBtn").style.display = "block";
        } else {
            document.getElementById("topBtn").style.display = "none";
        }
    }

// When the user clicks on the button, scroll to the top of the document
    topFunction(scrollDuration) {
        var scrollStep = -window.scrollY / (scrollDuration / 15),
            scrollInterval = setInterval(function(){
                if ( window.scrollY != 0 ) {
                    window.scrollBy( 0, scrollStep );
                }
                else clearInterval(scrollInterval);
            },15);
    }

    render() {
        return (
            <div align="center" className={"Page-content"}>
                {this.renderImg()}
                <div
                    onClick={()=>{this.topFunction(500)}} title="Go to top"
                    id={"topBtn"}>
                </div>
            </div>
        );
    }
}


export default Page;


