import React, {Component} from 'react';
import {Switch, Route, Link, withRouter} from 'react-router-dom'
import './Home/Home.css';
import Home from "./Home/Home";
import Page from "./Page/Page";
import {book} from "./Data/dataBook";
import {Carousel} from "react-bootstrap";
import Detail from "./Detail/Detail";


class App extends Component {

    constructor(props) {
        super(props);

        this.state = {
            value: {
                id: 0,
                chapter: 1,
            },
        }
    };

    render() {
        return (
            <div className="Home">
                <div className={"App-bar"}>
                    <button
                        className={"App-bar-button"}
                    >
                        <Link className={"App-bar-text"} to={"/"}>Home</Link>
                    </button>
                    <button
                        className={"App-bar-button"}>
                        <Link className={"App-bar-text"} to={"/Page"}>Page</Link>
                    </button>
                    <button
                        className={"App-bar-button"}>
                        <Link className={"App-bar-text"} to={"/About"}>About</Link>
                    </button>
                </div>
                <main>
                    <Switch>
                        <Route exact path='/' render={routeProps => <Home
                            onSelect={(value) => {
                                this.setState({value: value});
                            }}/>}/>
                        <Route path={"/Page/:string-chapter-:number"} render={routeProps =>
                            <Page book={book[this.state.value.id]} chapter={this.state.value.chapter}/>}/>
                        <Route path={"/:string"} render={routeProps =>
                            <Detail book={book[this.state.value.id]}/>}/>
                    </Switch>
                </main>
                <div className={"App-footer"}>
                    <text className={"App-footer-text clearfix"}>AbcXyz</text>
                    <br/>
                </div>
            </div>
        );
    }
}

export default withRouter(App);



