import React, {Component} from 'react';
import './Home.css';
import Item from "./Item";
import {book} from "../Data/dataBook"
import Grid from 'react-css-grid'
import "./Grid.css"
import {Carousel} from "react-bootstrap";
import {Link} from "react-router-dom";

class Home extends Component {

    constructor(props) {
        super(props);

    }


    drawItem() {

        var array = [];
        for (var i = 0; i < book.length; i++) {

            array.push(
                <div
                    key={i}
                    className={"Book-item"}>
                    <Item
                        onSelect={this.props.onSelect}
                        book={book[i]}/>
                </div>
            );
        }
        return <Grid align={"center"} gap={5} width={'15vw'}>{array}</Grid>;
    }

    render() {
        return (
            <div className={"Home"}>
                <Carousel
                    className={"Carousel"}
                    interval={2000}
                >
                    <Carousel.Item>
                        <div
                            className={"Carousel-img"}
                            style={{backgroundImage: `url("http://cdn.atomix.vg/wp-content/uploads/2018/04/OnePieceManga_Reverie.jpg")`}}>
                        </div>
                        <Carousel.Caption>
                            <Link to={`/${book[0].name}`} className={"Carousel-caption"}>One Piece</Link>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div
                            className={"Carousel-img"}
                            style={{backgroundImage: `url("http://otakukart.com/animeblog/wp-content/uploads/2017/05/naruto-shi.png")`}}>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div
                            className={"Carousel-img"}
                            style={{backgroundImage: `url("https://orig00.deviantart.net/1b8e/f/2016/162/3/3/untitled_drawing_by_americaseren99-da5wev9.png")`}}>
                        </div>
                    </Carousel.Item>
                </Carousel>
                <div className={"Grid-container"}>
                    {this.drawItem()}
                </div>
            </div>
        );
    }
}

export default Home;



