import React, {Component} from 'react';
import "./Item.css"
import {Link} from "react-router-dom";

class Item extends Component {

    constructor(props) {
        super(props);
        this.state ={
            book : this.props.book,
        };
    }


    render() {
        return (
            <nav className={"Item-container"} style={{backgroundImage : `url("${this.state.book.url}")`}}>
                <div className={"Item-content"}>
                    <Link to = {`/Page/${this.state.book.name}-chapter-${this.state.book.data.length}`}
                          onClick={()=>
                          {
                              this.props.onSelect({id : this.state.book.id , chapter : this.state.book.data.length});
                          }}
                          className={"Item-text-title"}
                          >{`Chapter ${this.state.book.data.length}`}</Link>
                </div>
            </nav>
        );
    }
}

export default Item;


